import { Component, OnInit } from '@angular/core';
import { GalletaLocalService } from '../core/services/autenticar/cookie.service';

import { User } from '../core/services/autenticar/user_model';
import { UserRegister } from '../core/services/autenticar/user_model';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {

  public user: User;
  public userRegister: UserRegister;
  constructor(
    private galletaLocalService: GalletaLocalService,
) {
   }

  ngOnInit(): void {
    this.user = this.galletaLocalService.getCurrentSession();
    this.userRegister = this.user.body[0];
  }


  public logout(): void {

    this.galletaLocalService.logout();

  }

}
